        <strong>{{row.title}}</strong>
        <div>
            <em class="text-muted small">{{row | readableEventDate}}</em><em v-if="row.firstLine" class="text-muted small"> - {{row.firstLine}}</em> 
            <div v-if="row.rooms && row.rooms.length"  class="text-muted small" ><strong>Rooms:</strong> <em>{{row.rooms | comma('title')}}</em></div>
        </div>

       
    </div>`,
});


let DefinitionCell = Vue.extend({
    props: {
        'row': {
            type: Object,
        },
        'column': {
            type: Object,
        },
