});


let FirstLineCell = Vue.extend({
    props: {
        'row': {
            type: Object,
        },
        'column': {
            type: Object,
        },
        'data': {
            // type: Object,
        },
    },
    template: `
     <div v-if="data"><em class="text-muted small" >{{data}}</em></div>
    `,
});




/////////////////////////////////////////

export default {
    props: {
        defaultView: {
            type: String,
            default: 'list',
        },
    },
    components: {
        FluroDataTable,
        FilterConditionGroup,
    },
    mounted() {
        // this.$refs.calendar.scrollToTime('08:00')
    },
    mixins: [SEOMixin, UserMixin, Layout],
    data() {

        var self = this;

        // var defaultCalendarOption = _.find(calendarOptions, {
        // value: self.defaultView,
        // })


        var datePeriodOptions = []
        var day = (1000 * 60 * 60 * 24);
        var week = day * 7;
        var now = new Date();


        //////////////////////////////////////////////////

        datePeriodOptions.push({
            title: 'Today',
            startDate: now,
            endDate: now,
        });

        datePeriodOptions.push({
            title: 'This Week',
            startDate: now,
            endDate: moment().add(1, 'weeks').toDate(),
        });

        datePeriodOptions.push({
            title: 'Last Week',
            startDate: moment().subtract(1, 'weeks').toDate(),
            endDate: now,
        });

        //////////////////////////////////////////////////

        datePeriodOptions.push({
            title: 'Next 4 weeks',
            startDate: now,
            endDate: moment().add(4, 'weeks').toDate(),
        });

        datePeriodOptions.push({
            title: 'Last 4 weeks',
            startDate: moment().subtract(4, 'weeks').toDate(),
            endDate: now,
        });

        //////////////////////////////////////////////////
        
        datePeriodOptions.push({
            title: 'Past 12 months',

            startDate: moment().subtract(12, 'months').toDate(),
            endDate: now,
        });


        var defaultDatePeriod = {
            startDate:now,
            endDate:moment().add(3, 'months').toDate(),
        };//datePeriodOptions[0]

        /////////////////////////////////////

        var viewModes = [

            //List/Default View
            {
                title: 'List',
                value: 'default',
                icon: 'bars',
            },


            // {
            //     title: 'Calendar',
            //     value: 'calendar',
            //     icon: 'calendar',
            // },

            // {
            //     title: 'Duplicates',
            //     value: 'duplicates',
            //     icon: 'users-class',
            // },

            // {
            //     title: 'Checkins',
            //     value: 'checkins',
            //     icon: 'sign-in',
            // },
        ];

        /////////////////////////////////////

        var initalFilter = defaultFilters;

        if (this.$route.query.filter && this.$route.query.filter.length) {
            initalFilter = JSON.parse(this.$route.query.filter);
        }

        /////////////////////////////////////

        return {

            // loading: false,

            // nowDate: null, //now,
            // eventMap: {},
            // timeMap: {},
            // monthMap: {},

            // calendar: defaultCalendarOption,
            // calendarOptions: calendarOptions,
            rawStartDate: defaultDatePeriod.startDate, //defaultStartDate,
            rawEndDate: defaultDatePeriod.endDate, //defaultEndDate,



            //New stuff
            search: '',
            searchFocussed: false,
            selection: this.$selection,
            viewModes: viewModes,
            viewMode: viewModes[0],
            showFilters: false,

            datePeriodOptions,

            rows: [], //this is so we can feed the subset into the filter configuration
            filterConfig: initalFilter,
            columns: [
                // { title: '', sortable: false },

                // { title: '', key: '_id', renderer: AvatarCell, sortable: false },

                { title: '', sortable: false, shrink: true, align: "center", key: 'realms', renderer: RealmDotCell },
                { title: 'Time', shrink: true, sortable: false, key: 'startDate', renderer: TimeCell, additionalFields: ['endDate'] },
                { title: 'Title', sortable: false, key: 'title', renderer: TitleCell, additionalFields: ['firstLine', 'status'] },
                { title: 'Definition', sortable: false, key: 'definition', renderer: DefinitionCell },
                { title: 'Location', sortable: false, key: 'locations', additionalFields: ['rooms'] },
                { title: 'Rooms', sortable: false, key: 'rooms' },
                // { title: '', sortable: false, key: 'firstLine', renderer: FirstLineCell, },

                // { title: 'Date', key: 'startDate', renderer: DateCell, additionalFields: ['endDate'] },
                // { title: 'First Name', key: 'firstName', renderer: FirstNameCell },
                // { title: 'Last Name', key: 'lastName', renderer: LastNameCell },
                // { type: 'integer', title: 'Age', shrink: true, key: 'age', sortType: 'number' },
                // { title: 'Gender', shrink: true, align: 'center', key: 'gender' },

                // { title: 'Household', shrink: true, key: 'family' },
                // { type: 'date', title: 'Updated', shrink: true, key: 'updated', renderer: DateCell, sortType: 'date' },
                // { type: 'date', title: 'Created', shrink: true, key: 'created', renderer: DateCell, sortType: 'date' },
                // { title: 'Status', sortable: false, key: 'status' },
            ],
        }
    },
    watch: {
        // 'filtered': 'remap',
        filterChangeString() {
            console.log('Filters changed', this.filterConfig);
            this.$router.replace({ query: Object.assign({}, this.$route.query, { filter: JSON.stringify(this.filterConfig) }) });
        }
    },
    created() {
        var self = this;

        // this.$watch(function() {
        //     // return JSON.stringify(self.$route.query);
        //     return self.defaultView, self.defaultDate;
        // }, self.routeChanged);

        // this.routeChanged();
    },
    computed: {

        activeViewMode() {
            var self = this;
            if (self.viewMode && self.viewMode.value) {
                return self.viewMode.value;
            }

            return 'default';
        },
        columnKeys() {
            return _.map(this.columns, 'key');
        },
        filterDebounce() {
            if (this.$vuetify.breakpoint.smAndDown) {
                return 1500;
            } else {
                return 700
            }
        },
        filterChangeString() {
            return FilterService.getFilterChangeString(this.filterConfig);
        },
        // filtered() {
            // return this.events;
        // },
        readableDate() {

            return this.$fluro.date.readableEventDate({
                startDate: this.startDate,
                endDate: this.endDate,
            }, 'day');
        },
        readableStartDate() {
            return this.$fluro.date.formatDate(this.startDate);
        },
        readableEndDate() {
            return this.$fluro.date.formatDate(this.endDate);
        },
        //We need the dates as strings for vuetify's datepicker component
        //not exactly sure why but this lets us bind the component to the date correctly
        startDate: {
            get() {
                return this.rawStartDate;
            },
            set(value) {

                var date = new Date(value);

                if (this.rawEndDate < date) {
                    this.rawEndDate = new Date(date);
                }

                return this.rawStartDate = new Date(value);
            }
        },
        endDate: {
            get() {
                return this.rawEndDate;
            },
            set(value) {

                var date = new Date(value);

                if (this.rawStartDate > date) {
                    this.rawStartDate = new Date(date);
                }

                return this.rawEndDate = new Date(value);
            }
        },


        startDateString: {
            get() {
                return this.stringDate(this.startDate);
            },
            set(value) {
                return this.startDate = new Date(value);
            }
        },
        endDateString: {
            get() {
                return this.stringDate(this.endDate);
            },
            set(value) {
                return this.endDate = new Date(value);
            }
        },
        // now: {
        //     get() {
        //         return this.nowDate;
        //     },
        //     set(date) {

        //         var dateString = this.stringDate(date);
        //         this.$router.push({ query: Object.assign({}, this.$route.query, { date: dateString }) });

        //         this.nowDate = date;
        //     },
        // },
        // nowMonthString() {
        //     return this.$fluro.date.formatDate(this.now, 'MMM YYYY');
        // },
    },
